:root {
	/* Sizes */
	
	--header-height: 35px;
	--tile-border-width: 4px;
	--possible-width: calc((100vw - 2 * var(--tile-border-width) * var(--field-width)) / var(--field-width));
	--possible-height: calc((100vh - var(--header-height) - 2 * var(--tile-border-width) * var(--field-height)) / var(--field-height));
	--tile-width: /*min(var(--possible-width), var(--possible-height))*/var(--possible-height);
	
	/* Colors */
	
	--tile-hidden-bg-color: #d8d8d8;
	--tile-hidden-border-tl-color: #fff;
	--tile-hidden-border-br-color: #aaa;
	
	--tile-visible-bg-color: #cbcbcb;
	--tile-visible-border-tl-color: #d8d8d8;
	--tile-visible-border-br-color: #bbb;
	--tile-visible-bomb-color: #f55;
	
	--tile-zero-fg-color: var(--tile-visible-bg-color);
	--tile-one-fg-color: #0000fc;
	--tile-two-fg-color: #017e00;
	--tile-three-fg-color: #fe0000;
	--tile-four-fg-color: #020080;
	--tile-five-fg-color: #7f0300;
	--tile-six-fg-color: #008080;
	--tile-seven-fg-color: #000000;
	--tile-eight-fg-color: #808080;
}


#minesweeper {
	#header {
		height: var(--header-height);
	}
	#game {
		display: grid;
		width: max-content;
		grid-template-columns: repeat(var(--field-width), 1fr);

		.tile {
			position: relative;
			width: var(--tile-width);
			height: var(--tile-width);
			border-width: var(--tile-border-width);
			border-style: solid;
			
			background-color: var(--tile-visible-bg-color);
			border-color: var(--tile-visible-border-tl-color) var(--tile-visible-border-br-color) var(--tile-visible-border-br-color) var(--tile-visible-border-tl-color);
			
			span {
				user-select: none;
				cursor: default;
				font-weight: bold;
				position: absolute;
				margin: 0;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				font-size: var(--tile-width);
			}
			
			@each $nbr in zero, one, two, three, four, five, six, seven, eight {
				&.#{$nbr} span {
					color: var(--tile-#{$nbr}-fg-color);
				}
			}
			
			&.bomb:not(.hidden) {
				background-color: var(--tile-visible-bomb-color);
				border-color: var(--tile-visible-bomb-color);
			}
			
			&.bomb {
				background-image: url(mine.png);
				background-size: contain;
				
				span {
					color: rgba(0, 0, 0, 0);
				}
			}
			
			&.hidden {
				background-color: var(--tile-hidden-bg-color);
				border-color: var(--tile-hidden-border-tl-color) var(--tile-hidden-border-br-color) var(--tile-hidden-border-br-color) var(--tile-hidden-border-tl-color);
			}
			
			&.flagged {
				background-image: url(flag.svg);
				background-position-x: calc(0px - var(--tile-border-width) + 1px);
				background-position-y: calc(0px - var(--tile-border-width) + 1px);
				background-size: calc(var(--tile-width) + 2 * var(--tile-border-width) - 2px);
			}
			
			&.unsure {
				&::after {
					content: "?";
					cursor: default;
					font-weight: bold;
					position: absolute;
					margin: 0;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					font-size: var(--tile-width);
				}
			}
		}
	}
}